/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 *
 * MENU RESPONSIVE
 * 	>1200px
 *  >992px 
 *  >768px 
 *  ≥576px  
 *  <576px
 */

 /** RESPONSIVE Extra Large >1200px*/
 .wp-block-codebaou-hal21-codebaou-hal21-menu {
	display:flex;
	position:relative;
	width:100% !important;
	max-width:100% !important;
	flex-direction: row;
	padding-right:25px;
	justify-content: end; /* ALINEAR HORIZONTAL */
	align-self:end; /* ALINEAR VERTICAL */
}

.wp-block-codebaou-hal21-codebaou-hal21-menu > div > ul{
	display:flex;
	flex-direction:row;
	margin:0 !important;
}

.wp-block-codebaou-hal21-codebaou-hal21-menu > div > ul > li{
	list-style:none;
	display:flex;
	flex-direction:row;
	margin:12px;
}

.wp-block-codebaou-hal21-codebaou-hal21-menu > div > ul > li > a{
	text-decoration: none;
}

.wp-block-codebaou-hal21-codebaou-hal21-menu svg{
	display:none;
}

@media(max-width:1024px){
	.wp-block-codebaou-hal21-codebaou-hal21-menu{
		flex-direction:row;
		justify-content: end; /* ALINEAR HORIZONTAL */
		align-self:center; /* ALINEAR VERTICAL */
	}

	.wp-block-codebaou-hal21-codebaou-hal21-menu > div{
		display:none;
		justify-content: center;
		position:fixed;
		top:0;
		left:0;
		min-width:100vw;
		opacity: none;
		z-index:2;
		overflow:none;
		background-color: rgba(14, 14, 14, 0.664); /* El último valor es la opacidad */
		animation-name:Animation-menu-open;
		animation-duration: 2s;
		animation-fill-mode:forwards;
		overflow: hidden;
	}

	.wp-block-codebaou-hal21-codebaou-hal21-menu > div > ul{
		display:flex;
		width:100%;
		flex-direction:column;
		margin:0;
		padding:0;
		padding-top:200px;
		min-width:100vw;
		min-height:100vh;
  		/* Difuminar el fondo detrás del elemento */
 		backdrop-filter: blur(5px);
		border-bottom-left-radius: 50vh;
	}

	.wp-block-codebaou-hal21-codebaou-hal21-menu > div > ul > li{
		display:flex;
		flex-direction:row;
		justify-content: center;
		padding-top:12px;
		padding-bottom:12px;
	}

	.wp-block-codebaou-hal21-codebaou-hal21-menu > div > ul > li > a{
		width:100%;
		text-align:center;
		padding-top:12px;
		padding-bottom:12px;
	}

	.wp-block-codebaou-hal21-codebaou-hal21-menu svg{
		display:block;
		width:30px;
		height:30px;
		z-index:4;
		position:absolute;

	}
}

@keyframes Animation-menu-open{
	0%{
		height:0vh;
		border-radius: 100%;
		min-height:0vh;
	}

	99%{
		height:100vh ;
		border-radius: 0%;
	}

	100%{
		min-height:100vh;
	}
}